.App {
  font-family: sans-serif;
  text-align: center;
}
.header-bg,
.footer-bg {
  background-color: #00445f;
  height: 25px;
  color: #fff;
  padding: 5px;
}
.footer-bg {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.searchbar {
  width: 80%;
  padding: 4px 0px;
  border: solid #00445f;
}
.searchbtn {
  background-color: #00445f;
  padding: 4px;
  border-radius: 0 5px 5px 0;
  color: #fff;
}
.borderbtm {
  border-bottom: 2px solid #cecece;
}
